



































































































































import Vue from "vue";
import TaskModel from "@/models/TaskModel";

import { mapState } from "vuex";

interface error {
  name: boolean;
  category: boolean;
  kind: boolean;
}

export default Vue.extend({
  name: "TaskCreate",
  props: {
    task: Object,
    isEditMode: Boolean,
    enableDueDatePick: Boolean,
  },
  data() {
    return {
      currTask: this.task as TaskModel,
      selectedDate: new Date(),
      selectedCategory: "" as string,
      errors: {
        name: true,
        category: true,
        kind: true,
      } as error,
    };
  },
  computed: {
    ...mapState(["categories"]),
    createText(): string {
      return this.isEditMode ? "Save" : "Create";
    },
    endDate(): string {
      return this.isTask() ? "Due" : "Ends";
    },
    title(): string {
      const mode = this.isEditMode ? "Edit" : "Create";
      return `${mode} a ${this.currTask.kind}`;
    },
  },
  mounted() {
    if (!this.isEditMode) {
      const title = document.getElementById("title")!;
      title.focus();
    }
    this.isTask();
  },
  watch: {
    "currTask.kind": {
      handler() {
        this.isTask();
      },
    },
    "currTask.category": {
      handler(category) {
        // Update task's color to match its category
        this.currTask.color = this.colorizeByCategory(category);
      },
    },
  },
  methods: {
    closeModal() {
      this.$emit("closeModal");
      this.$destroy();
    },
    storeTask() {
      // Check all inputs are filled
      if (this.missingRequired()) {
        return;
      } else if (this.isEditMode) {
        this.$store.dispatch("editTask", this.currTask);
      } else {
        this.$store.dispatch("saveTask", this.currTask);
      }
      this.closeModal();
    },
    missingRequired() {
      this.errors.name = !this.currTask.name ? false : true;
      this.errors.category = !this.currTask.category ? false : true;
      this.errors.kind = !this.currTask.kind ? false : true;
      if (!this.errors.name || !this.errors.category || !this.errors.kind) {
        return true;
      }
    },
    removeTask() {
      this.$store.dispatch("removeTask", this.currTask.id);
      this.closeModal();
    },
    isTask() {
      return this.currTask.kind == "Task" ? true : false;
    },
    colorizeByCategory(category: string): string {
      if (category === this.categories[0]) {
        return "#2dc9e2";
      } else if (category === this.categories[1]) {
        return "#d6be28";
      } else if (category === this.categories[2]) {
        return "#efa926";
      } else {
        return "#f69051";
      }
    },
  },
});
